<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title">
                
            </h4>
            <form>
                <div class="subtitle"> :</div>
                <textarea name="textarea" id="textarea" cols="30" rows="10" maxlength="2000" v-model="capitalStrength.capitalStrength">
                </textarea>
                <span>2000!</span>
                <el-button type="primary" @click="updataCapitalStrength" v-if="hasData"> </el-button>
                <el-button type="primary" @click="insertCapitalStrength" v-if="!hasData"> </el-button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            capitalStrength:{
                capitalStrength: ''
            },
            hasData: false
        }
    },
    components: {
    },
    created() {
        this.getCapitalStrength()
    },
    methods: {
        // 
        getCapitalStrength() {
            let id = localStorage.getItem('id')
            this.$http.get(`/strength/find.action?user_id=${id}`)
            .then( res => {
                if( res.data.responseDesc == '' && res.data.responseCode != 0 ){
                    this.capitalStrength = res.data.responseCode[0]
                    this.hasData = true
                    console.log(res.data)
                } else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                    this.capitalStrength.capitalStrength = ''
                    this.hasData = false
                }
            } ).catch( err => {
                console.log(err)
            } ) 
        },
        // 
        updataCapitalStrength() {
            const formData = JSON.stringify(this.capitalStrength)   
            this.updateInfo( 'strength/update.action', formData, this.getCapitalStrength)
        },
        //
        insertCapitalStrength() {
            this.capitalStrength.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.capitalStrength)
            this.$http({
                url: 'strength/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getCapitalStrength()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    }
}
</script>

<style lang="scss" scoped>
@import '../../../../css/form.scss';
@import '../../../../css/textarea.scss'
</style>




