<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title">
                
            </h4>
            <form>
                <div class="subtitle"> :</div>
                <textarea name="textarea" id="textarea" cols="30" rows="10" maxlength="2000" v-model="relationship.holderChange">
                </textarea>
                <span>2000!</span>
                <el-button type="primary" @click="updateRelationship" v-if="hasData"> </el-button>
                <el-button type="primary" @click="insertRelationship" v-if="!hasData"> </el-button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            relationship:{
            },
            hasData: false
        }
    },
    components: {
    },
    created() {
        this.getRelationship()
    },
    methods: {
        // 
        getRelationship() {
            let id = localStorage.getItem('id')
            this.$http.get(`/relationship/find.action?user_id=${id}`)
            .then( res => {
                if( res.data.responseDesc == '' && res.data.responseCode != 0 ){
                    this.relationship = res.data.responseCode[0]
                    this.hasData = true
                } else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                    this.hasData = false
                }
            } ).catch( err => {
                console.log(err)
            } ) 
        },
        // 
        updateRelationship() {
            const formData = JSON.stringify(this.relationship)   
            this.updateInfo( 'relationship/update.action', formData, this.getRelationship)
        },
        //
        insertRelationship() {
            this.relationship.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.relationship)
            this.$http({
                url: 'relationship/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getRelationship()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    }
}
</script>

<style lang="scss" scoped>
@import '../../../../css/form.scss';
@import '../../../../css/textarea.scss'

</style>




