<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">

                <div class="input-container">
                    <label for="dotNumber" style="width:200px;height:50px">:</label>
                    <input type="text" name="dotNumber" id="dotNumber" v-model="marketNetworkInfo.dotNumber"> 
                </div>

                <div class="input-container">
                    <label for="dotWay" style="width:200px;height:50px">:</label>
                    <select v-model="marketNetworkInfo.dotWay">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="dotSale" style="width:200px;height:50px">:</label>
                    <select v-model="marketNetworkInfo.dotSale">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="dotNanjin" style="width:200px;height:50px">:</label>
                    <select v-model="marketNetworkInfo.dotNanjin">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>  

                <div class="input-container">
                    <label for="dotPartnership" style="width:200px;height:50px">:</label>
                    <select v-model="marketNetworkInfo.dotPartnership">
                        <option value="GOOD"></option>
                        <option value="PREFER"></option>
                        <option value="NORMAL"></option>
                        <option value="WEAK"></option>
                    </select>
                </div> 

                <div class="input-container" style="margin-top:20px;">
                    <label for="dotDescribe" style="width:200px;height:50px;">:</label>
                    <textarea name="dotDescribe" id="dotDescribe" cols="30" rows="10" v-model="marketNetworkInfo.dotDescribe"></textarea>
                    <span style="margin-left: 214px">2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateMarketNetworkInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertMarketNetworkInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            marketNetworkInfo: {},
            hasData: false
        }
    },
    created() {
        this.getMarketNetworkInfo()
    },
    methods: {
        // 
        getMarketNetworkInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`marketnetwork/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.marketNetworkInfo = res.data.responseCode[0] 
                        this.hasData = true
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateMarketNetworkInfo() {
            let formData = JSON.stringify(this.marketNetworkInfo)
            this.$http({
                url: 'marketnetwork/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getMarketNetworkInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },

        //
        insertMarketNetworkInfo() {
            this.marketNetworkInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.marketNetworkInfo)
            this.$http({
                url: 'marketnetwork/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getMarketNetworkInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






