<template>
    <div class="enterprise-apply">
        <!--  -->
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="staffTotal">:</label>
                    <input type="text" name="staffTotal" v-model="staffInfo.staffTotal">
                </div>

                <div class="input-container">
                    <label for="fiveTotal">5:</label>
                    <input type="text" name="fiveTotal" v-model="staffInfo.fiveTotal">
                </div>

                <div class="input-container">
                    <label for="specialtyLower">:</label>
                    <input type="text" name="specialtyLower" v-model="staffInfo.specialtyLower">
                </div>

                <div class="input-container">
                    <label for="specialty">:</label>
                    <input type="text" name="specialty" v-model="staffInfo.specialty">
                </div>

                <div class="input-container">
                    <label for="undergraduate">:</label>
                    <input type="text" name="undergraduate" v-model="staffInfo.undergraduate">
                </div>

                <div class="input-container">
                    <label for="graduate">:</label>
                    <input type="text" name="graduate" v-model="staffInfo.graduate">
                </div>

                <div class="input-container">
                    <label for="thirtyLower">30:</label>
                    <input type="text" name="thirtyLower" v-model="staffInfo.thirtyLower">
                </div>

                <div class="input-container">
                    <label for="thirtyFortyfive">30-45:</label>
                    <input type="text" name="thirtyFortyfive" v-model="staffInfo.thirtyFortyfive">
                </div>

                <div class="input-container">
                    <label for="fortyfiveAbove">45:</label>
                    <input type="text" name="fortyfiveAbove" v-model="staffInfo.fortyfiveAbove">
                </div>

                <div class="input-container">
                    <label for="primary">:</label>
                    <input type="text" name="primary" v-model="staffInfo.primary">
                </div>

                <div class="input-container">
                    <label for="intermediate">:</label>
                    <input type="text" name="intermediate" v-model="staffInfo.intermediate">
                </div>

                <div class="input-container">
                    <label for="senior">:</label>
                    <input type="text" name="senior" v-model="staffInfo.senior">
                </div>

                <div class="input-container">
                    <label for="threeofficeLower">3:</label>
                    <input type="text" name="threeofficeLower" v-model="staffInfo.threeofficeLower">
                </div>

                <div class="input-container">
                    <label for="threeofficeFive">3-55:</label>
                    <input type="text" name="threeofficeFive" v-model="staffInfo.threeofficeFive">
                </div>

                <div class="input-container">
                    <label for="fiveofficeTen">5-1010:</label>
                    <input type="text" name="fiveofficeTen" v-model="staffInfo.fiveofficeTen">
                </div>

                <div class="input-container">
                    <label for="tenofficeAbove">10:</label>
                    <input type="text" name="tenofficeAbove" v-model="staffInfo.tenofficeAbove">
                </div>
                <input type="button" value="" id="btn" @click="updateEnterInfo" v-if="hasData"> 
                <input type="button" value="" id="btn" @click="insertEnterInfo" v-if="!hasData"> 
            </form>
        </div>

    </div>
</template>

<script>
export default {
    data() {
      return {
            staffInfo: {
            },
            hasData: false
      }
    },
    created() {
        this.getStaffInfo()
    },
    methods: {
        // 
        getStaffInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`staff/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length !=0 ){
                        this.staffInfo = res.data.responseCode[0]
                        this.hasData = true 
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        updateEnterInfo() {
            let formdata = JSON.stringify(this.staffInfo)
            this.$http({
                url: 'staff/update.action',
                method: 'post',
                data: formdata,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getStaffInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertEnterInfo() {
            this.staffInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.staffInfo)
            this.$http({
                url: 'staff/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getStaffInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
    components: {
    },
    
}
</script>

<style lang="scss" scoped>
@import '../../../../css/form.scss';
.input-container {
    label: {
        width: 100px !important;
    }
}
</style>




