<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="13"></td>
                        </tr>
                        <tr v-for="item in otherInvestInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.winningBid}}</td>
                            <td>{{item.ownerSide}}</td>
                            <td>{{item.ownerLink}}</td>
                            <td>{{item.ownerTelephone}}</td>
                            <td>{{item.bidTime}}</td>
                            <td>{{item.bidMoney}}</td>
                            <td>{{item.bidGood}}</td>
                            <td>{{item.investigationTime}}</td>
                            <td>{{item.investigationWay}}</td>
                            <td>{{item.investigationResult}}</td>
                            <td>{{item.letter}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- / dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="winningBid">:</label>
                    <input type="text" name="winningBid" v-model="form.winningBid">
                </div>

                <div class="input-container">
                    <label for="ownerSide">:</label>
                    <input type="text" name="ownerSide" v-model="form.ownerSide">
                </div>

                <div class="input-container">
                    <label for="ownerLink">:</label>
                    <input type="text" name="ownerLink" v-model="form.ownerLink">
                </div>

                <div class="input-container">
                    <label for="ownerTelephone">:</label>
                    <input type="text" name="ownerTelephone" v-model="form.ownerTelephone">
                </div>

                <div class="input-container">
                    <label for="bidTime">:</label>                    
                    <Datepicker class="datepicker" id="bidTime" v-model="form.bidTime" language="zh" format="yyyyMMdd"></Datepicker>
                </div>

                <div class="input-container">
                    <label for="bidMoney">:</label>
                    <input type="text" name="bidMoney" v-model="form.bidMoney">
                </div>

                <div class="input-container">
                    <label for="bidGood">:</label>
                    <select v-model="form.bidGood">
                        <option value="1"></option>
                        <option value="0"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="investigationTime">:</label>                    
                    <Datepicker class="datepicker" id="investigationTime" v-model="form.investigationTime" language="zh" format="yyyyMMdd"></Datepicker>
                </div>

                <div class="input-container">
                    <label for="investigationWay">:</label>
                    <input type="text" name="investigationWay" v-model="form.investigationWay">
                </div>

                <div class="input-container">
                    <label for="investigationResult">:</label>
                    <input type="text" name="investigationResult" v-model="form.investigationResult">
                </div>

                <div class="input-container">
                    <label for="letter">:</label>
                    <select name="letter" id="letter" v-model="form.letter">
                        <option value="1"></option>
                        <option value="0"></option>
                        <option value="2"></option>
                    </select>
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
export default {
    data() {
        return {
            otherInvestInfo: [],
            hasNoData: true,
            form: {
                winningBid: '',
                ownerSide: '',
                ownerLink: '',
                ownerTelephone: '',
                bidTime: '',
                bidMoney: '',
                bidGood: '',
                investigationTime: '',
                investigationWay: '',
                investigationResult: '',
                letter: ''         
            },
            addFormVisible: false,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
        Datepicker
    },
    created() {
        this.getOtherInvestInfo()
    },
    methods: {
        // 
         getOtherInvestInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`otherinvest/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        console.log(res.data)
                        this.otherInvestInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.otherInvestInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'otherinvest/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOtherInvestInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `otherinvest/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOtherInvestInfo()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'otherinvest/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOtherInvestInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"
</style>




