<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="passQuality" style="width:200px;height:50px">ISO9000:</label>
                    <select v-model="managequalityInfo.passQuality">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="qualityBook" style="width:200px;height:50px">:</label>
                    <select v-model="managequalityInfo.qualityBook">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="manageQuality" style="width:200px;height:50px">:</label>
                    <textarea name="manageQuality" id="manageQuality" cols="30" rows="10" v-model="managequalityInfo.manageQuality"></textarea>
                    <span>2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateManagesecurityInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertManagesecurityInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            managequalityInfo: {},
            hasData: false
        }
    },
    created() {
        this.getManagequalityInfo()
    },
    methods: {
        // 
        getManagequalityInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`managequality/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.managequalityInfo = res.data.responseCode[0]
                        this.hasData = true 
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateManagesecurityInfo() {
            let formData = JSON.stringify(this.managequalityInfo)
            this.$http({
                url: 'managequality/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getManagequalityInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },

        //
        insertManagesecurityInfo() {
            this.managequalityInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.managequalityInfo)
            this.$http({
                url: 'managequality/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getManagequalityInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






