<template>
    <div class="enterprise-apply">

        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="8"></td>
                        </tr>
                        <tr v-for="item in mechanicalInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.mechanicalName}}</td>
                            <td>{{item.mechanicalType}}</td>
                            <td>{{item.mechanicalPlace}}</td>
                            <td>{{item.number}}</td>
                            <td>{{item.originalValue}}</td>
                            <td>{{item.netWorth}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!--  dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="mechanicalName">:</label>
                    <input type="text" name="mechanicalName" v-model="form.mechanicalName">
                </div>

                <div class="input-container">
                    <label for="mechanicalType">:</label>
                    <input type="text" name="mechanicalType" v-model="form.mechanicalType">
                </div>

                <div class="input-container">
                    <label for="mechanicalPlace">:</label>
                    <input type="text" name="mechanicalPlace" v-model="form.mechanicalPlace">                    
                </div>

                <div class="input-container">
                    <label for="number">:</label>
                    <input type="text" name="number" v-model="form.number">
                </div>

                <div class="input-container">
                    <label for="originalValue">:</label>
                    <input type="text" name="originalValue" v-model="form.originalValue">
                </div>

                <div class="input-container">
                    <label for="netWorth">:</label>
                    <input type="text" name="netWorth" v-model="form.netWorth">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            mechanicalInfo:[],
            hasNoData: true,
            addFormVisible: false,
            form: {

            },
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
    },
    created() {
        this.getmechanicalInfo()
    },
    methods: {
        // 
         getmechanicalInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`mechanical/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        this.mechanicalInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.mechanicalInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'mechanical/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getmechanicalInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `mechanical/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getmechanicalInfo()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'mechanical/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getmechanicalInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"
    

</style>




