<template>
    <div class="enterprise-apply">

        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>   
                        <th></th>                                             
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="9"></td>
                        </tr>
                        <tr v-for="item in technicalInfo" :key="item.id">
                            <td>{{item.id}}</td>                            
                            <td>{{item.name}}</td>
                            <td>{{item.title}}</td>
                            <td>{{item.rankTitle}}</td>
                            <td>{{item.certificate}}</td>                            
                            <td>{{item.major}}</td>
                            <td>{{item.workChange}}</td>
                            <td>{{item.overdueNumber}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!--  dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">

                <div class="input-container">
                    <label for="name">:</label>
                    <input type="text" name="name" v-model="form.name">
                </div>

                <div class="input-container">
                    <label for="title">:</label>
                    <input type="text" name="title" v-model="form.title">
                </div>

                <div class="input-container">
                    <label for="rankTitle">:</label>
                    <input type="text" name="rankTitle" id="rankTitle" v-model="form.rankTitle">
                </div>

                <div class="input-container">
                    <label for="major">:</label>
                    <input type="text" name="major" v-model="form.major">
                </div>

                <div class="input-container">
                    <label for="certificate">:</label>
                    <input type="text" name="certificate" v-model="form.certificate">
                </div>

                <div class="input-container">
                    <label for="workChange">:</label>
                    <input type="text" name="workChange" v-model="form.workChange">
                </div>

                <div class="input-container">
                    <label for="overdueNumber">:</label>
                    <input type="text" name="overdueNumber" v-model="form.overdueNumber">
                </div>

            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            technicalInfo: [],
            hasNoData:true,
            form: {
                id: '',
                name: '',
                title: '',
                rankTitle: '',
                major: '',
                certificate: '',
                workChange: '',
                overdueNumber: '',
            },
            addFormVisible: false,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
    },
    created() {
        this.getTechnical()
    },
    methods: {
        // 
         getTechnical() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`artisan/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        this.technicalInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.technicalInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'artisan/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTechnical()  // 
                }
            } ).catch( err => {
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `artisan/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTechnical()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'artisan/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTechnical()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss";

</style>




