<template>
    <div class="enterprise-apply">

        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="9"></td>
                        </tr>
                        <tr v-for="item in qualifications" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.qualificationsName}}</td>
                            <td>{{item.qualificationsType}}</td>
                            <td>{{item.qualificationsGrade}}</td>
                            <td>{{item.qualificationsNumber}}</td>
                            <td>{{item.qualificationsOffice}}</td>
                            <td>{{item.firstDate}}</td>
                            <td>{{item.endDate}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!--  dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="qualificationsName">:</label>
                    <input type="text" name="qualificationsName" v-model="form.qualificationsName">
                </div>

                <div class="input-container">
                    <label for="qualificationsType">:</label>
                    <input type="text" name="qualificationsType" v-model="form.qualificationsType">
                </div>

                <div class="input-container">
                    <label for="qualificationsGrade">:</label>
                    <input type="text" name="qualificationsGrade" id="qualificationsGrade" v-model="form.qualificationsGrade">
                </div>

                <div class="input-container">
                    <label for="qualificationsNumber">:</label>
                    <input type="text" name="qualificationsNumber" v-model="form.qualificationsNumber">
                </div>

                <div class="input-container">
                    <label for="qualificationsOffice">:</label>
                    <input type="text" name="qualificationsOffice" v-model="form.qualificationsOffice">
                </div>

                <div class="input-container">
                    <label for="firstDate">:</label>
                    <Datepicker class="datepicker" id="firstDate" v-model="form.firstDate" language="zh" format="yyyyMMdd"></Datepicker>
                </div>

                <div class="input-container">
                    <label for="endDate">:</label>
                    <Datepicker class="datepicker" id="endDate" v-model="form.endDate" language="zh" format="yyyyMMdd"></Datepicker>

                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
export default {
    data() {
        return {
            qualifications: [],
            hasNoData: true,
            form : {
                qualificationsName: '',
                qualificationsType: '',
                qualificationsGrade: '',
                qualificationsNumber: '',
                qualificationsOffice: '',
                firstDate: '',
                endDate: ''
            },
            addFormVisible: false,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
        Datepicker
    },
    created() {
        this.getQualifications()
    },
    methods: {
        // 
         getQualifications() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`qualifications/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if( res.data.responseDesc == '') {
                        this.qualifications = res.data.responseCode
                    }else {
                        return 
                    }
                }
            ).then( () => {
                // 
                if( this.qualifications.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'qualifications/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getQualifications()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `qualifications/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getQualifications()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'qualifications/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getQualifications()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"
</style>




