export function updateInfo( url, formData, callback ) {
    this.$http({
        url: url,
        method: 'post',
        data: formData
    }).then( res => {
        if( res.data.responseDesc == '' ) {
            this.$message({
                message: '',
                type: 'success'
            })
        }else {
            this.$message({
                message: ',',
                type: 'error'
            })
        }
    }).then( () => {
        callback()
    }).catch( err => {
        this.$message({
            message: ',',
            type: 'error'
        })
    })
}


