<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="planning" style="width:200px;height:50px">3:</label>
                    <select v-model="strategyInfo.planning">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="measures" style="width:200px;height:50px">:</label>
                    <select v-model="strategyInfo.measures">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="strategySm" style="width:200px;height:50px">:</label>
                    <textarea name="strategySm" id="strategySm" cols="30" rows="10" v-model="strategyInfo.strategySm"></textarea>
                    <span>2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateStrategyInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertStrategyInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            strategyInfo: {},
            hasData: false
        }
    },
    created() {
        this.getStrategyInfo()
    },
    methods: {
        // 
        getStrategyInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`strategy/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.strategyInfo = res.data.responseCode[0] 
                        this.hasData = true
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateStrategyInfo() {
            let formData = JSON.stringify(this.strategyInfo)
            this.$http({
                url: 'strategy/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getStrategyInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertStrategyInfo() {
            this.strategyInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.strategyInfo)
            this.$http({
                url: 'strategy/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getStrategyInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






