<template>
    <div class="enterprise-apply">
        <div class="data-container">
        <h4 class="title"></h4>
        <div class="table-container">
            <el-button type="primary" id="add" @click="addItem"></el-button>
            <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th>()</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="hasNoData">
                            <td colspan="7"></td>
                    </tr>
                    <tr v-for="item in holderInfo" :key="item.id">
                        <td>{{item.id}}</td>
                        <td>{{item.holderName}}</td>
                        <td>{{item.holderMoney}}</td>
                        <td>{{item.currencyType}}</td>
                        <td>{{item.proportion}}</td>
                        <td>{{item.holderType}}</td>
                        <td>
                            <el-button type="primary" plain @click="updateItem(item)"></el-button>
                            <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        </div>

        <!--  dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="holderName">:</label>
                    <input type="text" name="holderName" v-model="form.holderName">
                </div>

                <div class="input-container">
                    <label for="holderMoney">():</label>
                    <input type="text" name="holderMoney" v-model="form.holderMoney">
                </div>

                <div class="input-container">
                    <label for="currencyType">:</label>
                    <input type="text" name="currencyType" v-model="form.currencyType">
                </div>

                <div class="input-container">
                    <label for="proportion">:</label>
                    <input type="text" name="proportion" id="proportion" v-model="form.proportion">
                </div>

                <div class="input-container">
                    <label for="holderType">:</label>
                    <input type="text" name="holderType" v-model="form.holderType">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            holderInfo: [],
            hasNoData: false,
            addFormVisible:false,
            form: {
                holderName: '',
                holderMoney: '',
                proportion: '',
                holderType: '',
                currencyType: ''
            },
            isUpdate: false,
            isInsert: true
        }
    },
    components: {
    },
    created() {
        this.getOwnershipStructure()
    },
    methods: {
        getOwnershipStructure() {
            let id = localStorage.getItem('id')
            this.$http.get(`holder/find.action?user_id=${id}`)
            .then( res => {
                    if( res.data.responseDesc == '' ) {
                        this.holderInfo = res.data.responseCode
                    } else {
                    }
                }
            ).then( () => {
                // 
                if( this.holderInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id            
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'holder/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    console.log(res.data)
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOwnershipStructure()  // 
                }else {
                    console.log(res.data)
                }
            } ).catch( err => {
                if( err ) {
                    console.log(err)
                    this.hasNoData = true
                    this.$message( {
                        message: '',
                        type: 'error'
                    } )
                }
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `holder/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOwnershipStructure()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },

        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'holder/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getOwnershipStructure()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss";

</style>




