<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="6"></td>
                        </tr>
                        <tr v-for="item in branchInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.branchRelationship}}</td>
                            <td>{{item.branchName}}</td>
                            <td>{{item.branchAddress}}</td>
                            <td>{{item.telephone}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- dialog -->
        <el-dialog title=" " :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="branchRelationship">:</label>
                    <input type="text" name="branchRelationship" v-model="form.branchRelationship">
                </div>

                <div class="input-container">
                    <label for="branchName">:</label>
                    <input type="text" name="branchName" v-model="form.branchName">
                </div>

                <div class="input-container">
                    <label for="branchAddress">:</label>
                    <input type="text" name="branchAddress" id="branchAddress" v-model="form.branchAddress">
                </div>

                <div class="input-container">
                    <label for="telephone">:</label>
                    <input type="text" name="telephone" v-model="form.telephone">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" v-if="isInsert" @click="handleAdd"> </el-button>
                <el-button type="primary" v-if="isUpdate" @click="handleUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            branchInfo: [],
            hasNoData: true,
            addFormVisible: false,
            form: {
                branchRelationship: '',
                branchName: '',
                branchAddress: '',
                telephone: ''
            },
            isUpdate: false,
            isInsert: true
        }
    },
    components: {
    },
    created() {
        this.getbranchInfo()
    },
    methods: {
        // 
        getbranchInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`branch/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        this.branchInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.branchInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'branch/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getbranchInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `branch/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getbranchInfo()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'branch/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getbranchInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss";
    

</style>




