<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem" style="margin-left: 42px;"></el-button>
                <div style="overflow-x:auto;padding:0 20px;">
                    <table border="1" style="width:95%">
                        <thead>
                            <tr>
                                <th></th>
                                <th v-for="item in cashInformation" :key="item.id">{{item.yeat}}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">{{item.cashXslw}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">{{item.cashJyhd}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">{{item.cashTzhd}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">{{item.cashCzhd}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">{{item.cashXjdj}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td v-for="item in cashInformation" :key="item.id">
                                    <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                    <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                                </td>
                            </tr>

                        </tbody>
                    </table>
                </div>

            </div>
        </div>

        <!--  dialog -->
        <el-dialog title=" " :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="yeat">:</label>
                    <select v-model="form.yeat">
                        <option value="2014">2014</option>
                        <option value="2015">2015</option>
                        <option value="2016">2016</option>
                        <option value="2017">2017</option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="cashXslw">:</label>
                    <input type="text" name="cashXslw" v-model="form.cashXslw">
                </div>

                <div class="input-container">
                    <label for="cashJyhd">:</label>
                    <input type="text" name="cashJyhd" v-model="form.cashJyhd">
                </div>

                <div class="input-container">
                    <label for="cashTzhd">:</label>
                    <input type="text" name="cashTzhd" v-model="form.cashTzhd">
                </div>

                <div class="input-container">
                    <label for="cashCzhd">:</label>
                    <input type="text" name="cashCzhd" v-model="form.cashCzhd">
                </div>

                <div class="input-container">
                    <label for="cashXjdj">:</label>
                    <input type="text" name="cashXjdj" v-model="form.cashXjdj">
                </div>

            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

export default {
    data() {
        return {
            cashInformation: [],
            form: { 
                yeat: '',
                cashXslw: '',
                cashJyhd: '',
                cashTzhd: '',
                cashCzhd: '',
                cashXjdj: ''
            },
            addFormVisible: false,
            formLabelWidth: '120px',
            hasNoData: false,
            isUpdate: false,
            isInsert: true
        }
    },
    components: {
    },
    created() {
        this.getCashInformation()
    },
    methods: {
        // 
         getCashInformation() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`cashinformation/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if( res.data.responseDesc == '' ) {
                        this.cashInformation = res.data.responseCode
                    }else {

                    }
                }
            ).then( () => {
                // 
                if( this.cashInformation.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'cashinformation/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getCashInformation()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `cashinformation/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getCashInformation()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'cashinformation/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getCashInformation()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        }

    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss";
#ldzc {
    padding: 20px 0 10px 23px;
    font-size: 14px;
    font-weight: 700;
}
</style>




