<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="6"></td>
                        </tr>
                        <tr v-for="item in historyInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.preChange}}</td>
                            <td>{{item.postChange}}</td>
                            <td>{{item.changeDate}}</td>
                            <td>{{item.changeProject}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!--  dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="preChange">:</label>
                    <input type="text" name="preChange" v-model="form.preChange">
                </div>

                <div class="input-container">
                    <label for="postChange">:</label>
                    <input type="text" name="postChange" v-model="form.postChange">
                </div>

                <div class="input-container">
                    <label for="changeDate">:</label>
                    <Datepicker class="datepicker" id="foundingTime" v-model="form.changeDate" language="zh" format="yyyyMMdd"></Datepicker>
                </div>

                <div class="input-container">
                    <label for="changeProject">:</label>
                    <input type="text" name="changeProject" v-model="form.changeProject">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
    data() {
        return {
            historyInfo: [
            ],
            form: { 
                preChange: '',
                postChange: '',
                changeDate: '',
                changeProject: ''
            },
            addFormVisible: false,
            formLabelWidth: '120px',
            hasNoData: false,
            isUpdate: false,
            isInsert: true
        }
    },
    components: {
        Datepicker
    },
    created() {
        this.getHistory()
    },
    methods: {
        // 
         getHistory() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`history/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        this.historyInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.historyInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'history/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getHistory()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `history/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getHistory()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'history/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getHistory()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss";

</style>




