<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="compuCertification" style="width:200px;height:50px">:</label>
                    <select v-model="certificationInfo.compuCertification">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="licence" style="width:200px;height:50px">:</label>
                    <select v-model="certificationInfo.licence">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container" style="margin-top:20px;">
                    <label for="certificationDescribe" style="width:200px;height:50px;">:</label>
                    <textarea name="certificationDescribe" id="certificationDescribe" cols="30" rows="10" v-model="certificationInfo.certificationDescribe"></textarea>
                    <span style="margin-left: 214px">2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateCertificationInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertCertificationInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            certificationInfo: {},
            hasData: false
        }
    },
    created() {
        this.getCertificationInfo()
    },
    methods: {
        // 
        getCertificationInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`certification/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.certificationInfo = res.data.responseCode[0] 
                        this.hasData = true
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateCertificationInfo() {
            let formData = JSON.stringify(this.certificationInfo)
            this.$http({
                url: 'certification/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getCertificationInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertCertificationInfo() {
            this.certificationInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.certificationInfo)
            this.$http({
                url: 'certification/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getCertificationInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






