<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="negativeEffect" style="width:300px;height:50px">:</label>
                    <select v-model="environmentInfo.negativeEffect">
                        <option value="1"></option>
                        <option value="0"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="industrialStructure" style="width:300px;height:50px">:</label>
                    <select v-model="environmentInfo.industrialStructure">
                        <option value="1"></option>
                        <option value="2"></option>
                        <option value="3"></option>
                        <option value="4"></option>
                    </select>
                </div>

                <div class="input-container" style="margin-top:20px;">
                    <label for="environSum" style="width:300px;height:50px;">:</label>
                    <textarea name="" id="" cols="30" rows="10" v-model="environmentInfo.environSum"></textarea>
                    <span style="margin-left: 314px">2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateEnvironmentInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertEnvironmentInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            environmentInfo: {},
            hasData: false
        }
    },
    created() {
        this.getEnvironmentInfo()
    },
    methods : {
        // 
        getEnvironmentInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`environment/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.environmentInfo = res.data.responseCode[0] 
                    }else {
                        return
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateEnvironmentInfo() {
            let formData = JSON.stringify(this.environmentInfo)
            this.$http({
                url: 'environment/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getEnvironmentInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertEnvironmentInfo() {
            this.environmentInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.environmentInfo)
            this.$http({
                url: 'environment/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getEnvironmentInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






