<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="equipmentRate" style="width:200px;height:50px">/:</label>
                    <input type="text" v-model="equipmentInfo.equipmentRate"> <i>/</i>
                </div>

                <div class="input-container">
                    <label for="productionProcess" style="width:200px;height:50px">:</label>
                    <select v-model="equipmentInfo.productionProcess">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="equipmentSm" style="width:200px;height:50px">:</label>
                    <textarea name="equipmentSm" id="equipmentSm" cols="30" rows="10" v-model="equipmentInfo.equipmentSm"></textarea>
                    <span>2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateEquipmentInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertEquipmentInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            equipmentInfo: {},
            hasData: false
        }
    },
    created() {
        this.getEquipmentInfo()
    },
    methods: {
        // 
        getEquipmentInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`equipment/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.equipmentInfo = res.data.responseCode[0]
                        this.hasData = true 
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateEquipmentInfo() {
            let formData = JSON.stringify(this.equipmentInfo)
            this.$http({
                url: 'equipment/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getEquipmentInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertEquipmentInfo() {
            this.equipmentInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.equipmentInfo)
            this.$http({
                url: 'equipment/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getEquipmentInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






