<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="9"></td>
                        </tr>
                        <tr v-for="item in tenderInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.mainFacts}}</td>
                            <td>{{item.type}}</td>
                            <td>{{item.deparName}}</td>
                            <td>{{item.date}}</td>
                            <td>{{item.consequence}}</td>
                            <td>{{item.severity}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- / dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="mainFacts">:</label>
                    <input type="text" name="mainFacts" v-model="form.mainFacts">
                </div>

                <div class="input-container">
                    <label for="type">:</label>
                    <input type="text" name="type" v-model="form.type">
                </div>

                <div class="input-container">
                    <label for="deparName">:</label>
                    <input type="text" name="deparName" v-model="form.deparName">
                </div>

                <div class="input-container">
                    <label for="date">:</label>                    
                    <Datepicker class="datepicker" id="date" v-model="form.date" language="zh" format="yyyyMMdd"></Datepicker>

                </div>

                <div class="input-container">
                    <label for="consequence">:</label>
                    <input type="text" name="consequence" v-model="form.consequence">
                </div>

                <div class="input-container">
                    <label for="severity">:</label>
                    <input type="text" name="v" v-model="form.severity">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
export default {
    data() {
        return {
            tenderInfo: [],
            hasNoData: true,
            form: {
                mainFacts: '',
                type: '',
                deparName: '',
                date: '',
                consequence: '',
                severity: ''            
            },
            addFormVisible: false,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
        Datepicker
    },
    created() {
        this.getTenderInfo()
    },
    methods: {
        // 
         getTenderInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`tender/find.action?user_id=${enterid}`).then(
                res => {
                    if( res.data.responseDesc == '' ) {
                        this.tenderInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.tenderInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'tender/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTenderInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `tender/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTenderInfo()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'tender/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getTenderInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"
</style>




