<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="8"></td>
                        </tr>
                        <tr v-for="item in winprojectInfo" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.winProject}}</td>
                            <td>{{item.winTime}}</td>
                            <td>{{item.winPrize}}</td>
                            <td>{{item.winType}}</td>
                            <td>{{item.winDepartment}}</td>
                            <td>{{item.remarks}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- / dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="winProject">:</label>
                    <input type="text" name="winProject" v-model="form.winProject">
                </div>

                <div class="input-container">
                    <label for="winTime">:</label>
                    <Datepicker class="datepicker" id="winTime" v-model="form.winTime" language="zh" format="yyyyMMdd"></Datepicker>  
                </div>

                <div class="input-container">
                    <label for="winPrize">:</label>
                    <input type="text" name="winPrize" v-model="form.winPrize">
                </div>

                <div class="input-container">
                    <label for="type">:</label>
                    <select name="type" id="type" v-model="form.winType">
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="winDepartment">:</label>
                    <input type="text" name="winDepartment" v-model="form.winDepartment">
                </div>

                <div class="input-container">
                    <label for="remarks">:</label>
                    <input type="text" name="changePmoneyroject" v-model="form.remarks">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
    data() {
        return {
            winprojectInfo: [],
            addFormVisible: false,
            form: {
                winProject: '',
                winTime: '',
                winPrize: '',
                winType: '',
                winDepartment: '',
                remarks: ''
            },
            hasNoData: true,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
        Datepicker
    },
    created() {
        this.getWinprojectInfo()
    },
    methods: {
        // 
         getWinprojectInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`winproject/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if( res.data.responseDesc == '' ) {
                        this.winprojectInfo = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.winprojectInfo.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'winproject/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getWinprojectInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `winproject/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getWinprojectInfo()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'winproject/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getWinprojectInfo()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"

</style>




