<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <div class="table-container">
                <el-button type="primary" id="add" @click="addItem"></el-button>
                <table border="1">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                    <tbody>
                        <tr v-if="hasNoData">
                            <td colspan="6"></td>
                        </tr>
                        <tr v-for="item in bankreference" :key="item.id">
                            <td>{{item.id}}</td>
                            <td>{{item.project}}</td>
                            <td>{{item.type}}</td>
                            <td>{{item.number}}</td>
                            <td>{{item.money}}</td>
                            <td>
                                <el-button type="primary" plain @click="updateItem(item)"></el-button>
                                <el-button type="primary" plain @click="deleteItem(item.id)"></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- / dialog -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <form class="form">
                <div class="input-container">
                    <label for="project">:</label>
                    <input type="text" name="project" v-model="form.project">
                </div>

                <div class="input-container">
                    <label for="type">:</label>
                    <select name="type" id="type" v-model="form.type">
                        <option value=""></option>
                        <option value=""></option>
                        <option value="/">/</option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="number">:</label>
                    <input type="text" name="number" v-model="form.number">
                </div>

                <div class="input-container">
                    <label for="money">:</label>
                    <input type="text" name="changePmoneyroject" v-model="form.money">
                </div>
            </form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleAdd" v-if="isInsert"> </el-button>
                <el-button type="primary" @click="handleUpdate" v-if="isUpdate"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            bankreference: [],
            addFormVisible: false,
            form: {
                project: '',
                type: '',
                number: '',
                money: ''
            },
            hasNoData: true,
            isInsert: true,
            isUpdate: false
        }
    },
    components: {
    },
    created() {
        this.getBankreference()
    },
    methods: {
        // 
         getBankreference() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`bankreference/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if( res.data.responseDesc == '' ) {
                        this.bankreference = res.data.responseCode
                    }else {
                    }
                }
            ).then( () => {
                // 
                if( this.bankreference.length ) {
                    this.hasNoData = false
                } else {
                    this.hasNoData = true
                }
            } ).catch( err => {
                // console.log(err)
                //
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )                   
            } )
            
        },
        // 
        addItem() {
            //dialog
            this.addFormVisible = true
            this.isInsert = true
            this.isUpdate = false
        },
        handleAdd() {
            this.form.userId = localStorage.getItem('id')
            delete this.form.id                        
            let formdata = JSON.stringify(this.form)
            // 
            this.$http({
                url: 'bankreference/insert.action',
                method: 'post',
                data: formdata
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getBankreference()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        },

        // 
        deleteItem(id) {
            this.$http({
                url: `bankreference/delete.action?id=${id}`,
                method: 'get'
            }).then( res => {
                if( res.data.responseCode == 0 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getBankreference()
                }
            } ).catch( err => {
                console.log( err )
            } )
        },
        // 
        updateItem(item) {
            this.addFormVisible = true
            this.form = item
            this.isUpdate = true
            this.isInsert = false
        },
        handleUpdate() {
            let formData = JSON.stringify(this.form)
            this.$http({
                url: 'bankreference/update.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getBankreference()  // 
                }
            } ).catch( err => {
                this.hasNoData = true
                this.$message( {
                        message: '',
                        type: 'error'
                } )
            } )
            this.addFormVisible = false // dialog
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
@import "../../../../css/table.scss";
@import "../../../../css/dialog.scss"

</style>




