<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="newEnterprises" style="width:200px;height:50px">:</label>
                    <select v-model="rbcapabilityInfo.newEnterprises">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="previousYear" style="width:200px;height:50px">:</label>
                    <input type="text"  v-model="rbcapabilityInfo.previousYear">
                </div>

                <div class="input-container">
                    <label for="bigpreviousYear" style="width:200px;height:50px">:</label>
                    <input type="text"  v-model="rbcapabilityInfo.bigpreviousYear">
                </div>

                <div class="input-container">
                    <label for="lastYear" style="width:200px;height:50px">:</label>
                    <input type="text"  v-model="rbcapabilityInfo.lastYear">
                </div>

                <div class="input-container">
                    <label for="prove" style="width:200px;height:50px">:</label>
                    <select v-model="rbcapabilityInfo.prove">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="awards" style="width:200px;height:50px"></label>
                    <input type="text" v-model="rbcapabilityInfo.awards" >
                </div>

                <div class="input-container">
                    <label for="rbcapabilitySm" style="width:200px;height:50px">:</label>
                    <textarea name="rbcapabilitySm" id="rbcapabilitySm" cols="30" rows="10" v-model="rbcapabilityInfo.rbcapabilitySm"></textarea>
                    <span>2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateRbcapabilityInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertRbcapabilityInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            rbcapabilityInfo: {},
            hasData: false
        }
    },
    created() {
        this.getRbcapabilityInfo()
    },
    methods: {
        // 
        getRbcapabilityInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`rbcapability/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.rbcapabilityInfo = res.data.responseCode[0] 
                    }else {
                        return
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateRbcapabilityInfo() {
            let formData = JSON.stringify(this.rbcapabilityInfo)
            this.$http({
                url: 'rbcapability/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getRbcapabilityInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertRbcapabilityInfo() {
            this.rbcapabilityInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.rbcapabilityInfo)
            this.$http({
                url: 'rbcapability/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getRbcapabilityInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






