<template>
    <div class="enterprise-apply">
        <div class="data-container">
            <h4 class="title"></h4>
            <form class="form">
                <div class="input-container">
                    <label for="starRating" style="width:270px;height:50px">GB/T279222011:</label>
                    <select v-model="manageserviceInfo.starRating">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersaleService" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersaleService">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersalePeople" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersalePeople">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersaleAddress" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersaleAddress">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersaleElectronic" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersaleElectronic">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersaleRecord" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersaleRecord">
                        <option value="true"></option>
                        <option value="false"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="aftersaleLevel" style="width:270px;height:50px">:</label>
                    <select v-model="manageserviceInfo.aftersaleLevel">
                        <option value="GOOD"></option>
                        <option value="PREFER"></option>
                        <option value="NORMAL"></option>
                        <option value="WEAK"></option>
                    </select>
                </div>

                <div class="input-container">
                    <label for="serviceQuality" style="width:270px;height:50px">:</label>
                    <textarea name="serviceQuality" id="serviceQuality" cols="30" rows="10" v-model="manageserviceInfo.serviceQuality"></textarea>
                </div>

                <div class="input-container">
                    <label for="briefIntroduction" style="width:270px;height:50px">:</label>
                    <textarea name="briefIntroduction" id="briefIntroduction" cols="30" rows="10" v-model="manageserviceInfo.briefIntroduction"></textarea>
                    <span>2000!</span>
                </div>
            </form>
            <el-button type="primary" id="submit" @click="updateManageserviceInfo" v-if="hasData"></el-button>
            <el-button type="primary" id="submit" @click="insertManageserviceInfo" v-if="!hasData"></el-button>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            manageserviceInfo: {},
            hasData: false
        }
    },
    created() {
        this.getManageserviceInfo()
    },
    methods: {
        // 
        getManageserviceInfo() {
            let enterid = localStorage.getItem('id')
            this.$http.get(`manageservice/find.action?user_id=${enterid}`).then(
                res => {
                    console.log(res.data)
                    if(res.data.responseDesc == '' && res.data.responseCode.length != 0){
                        this.manageserviceInfo = res.data.responseCode[0]
                        this.hasData = true
                    }else if(res.data.responseDesc == '' && res.data.responseCode.length == 0){
                        this.hasData = false
                    }
                }
            ).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            } )
        },
        // 
        updateManageserviceInfo() {
            let formData = JSON.stringify(this.manageserviceInfo)
            this.$http({
                url: 'manageservice/update.action',
                method: 'post',
                data: formData,

            }).then( res => {
                if( res.data.responseDesc == '' ) {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
                this.getManageserviceInfo()
            }).catch( err => {
                if(err) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                }
            })
        },
        //
        insertManageserviceInfo() {
            this.manageserviceInfo.userId = localStorage.getItem('id')
            let formData = JSON.stringify(this.manageserviceInfo)
            this.$http({
                url: 'manageservice/insert.action',
                method: 'post',
                data: formData
            }).then( res => {
                if( res.data.responseCode == 1 ) {
                    // 
                    this.$message( {
                        message: '',
                        type: 'success'
                    } )
                    this.getManageserviceInfo()  // 
                }
            } ).catch( err => {
                this.$message( {
                    message: '',
                    type: 'error'
                } )
            } )
        },
    },
    
}
</script>

<style lang="scss" scoped>
@import "../../../../css/form.scss";
#submit {
    display: block;
    margin: 10px auto;
}
textarea {
    padding: 5px 10px;
    font-family: "Microsoft Yahei";
    color: #666;
}
</style>






